<script>
    const daysInWeek = ["Dom", "Seg", "Ter", "Qua", "Qui", "Sex", "Sab"];

    export default {
        name: "FBarDate",

        props: {
            lastUpdate: {
                type: Number,
                default: null
            },

            disabledNav: {
                type: Boolean,
                default: false
            },

            showDay: {
                type: Boolean,
                default: false
            }
        },

        data: () => ({
            selectedDate: Date.now()
        }),

        computed: {
            title() {
                const _selectedDate = new Date(this.selectedDate);
                const day = daysInWeek[_selectedDate.getDay()];

                return `${day} - ${_selectedDate.toLocaleDateString("pt-BR")}`;
            },

            dateSelectedIstoday() {
                const _selectedDate = new Date(this.selectedDate);

                return (
                    _selectedDate.toLocaleDateString() ===
                    new Date().toLocaleDateString()
                );
            },

            descriptionLastUpdate() {
                const lastUpdate = new Date(this.lastUpdate);
                const _selectedDate = new Date(this.selectedDate);

                let buffer = lastUpdate.toLocaleTimeString("pt-BR");

                const equals =
                    _selectedDate.toLocaleDateString() ===
                    lastUpdate.toLocaleDateString();

                if (!equals && this.showDay) {
                    buffer += ` em ${lastUpdate.toLocaleDateString("pt-BR")}`;
                }

                return buffer;
            }
        },

        watch: {
            selectedDate: function(date) {
                this.$emit("change", date);
            }
        },

        methods: {
            changeDate(to) {
                const date = new Date(this.selectedDate);
                date.setDate(date.getDate() + to);

                this.selectedDate = date.getTime();
            },

            addOneDay() {
                this.changeDate(1);
            },

            subOneDay() {
                this.changeDate(-1);
            },

            reset() {
                this.selectedDate = Date.now();
            }
        }
    };
</script>

<template>
    <div class='fluid__bardate'>
        <v-btn flat icon :disabled='disabledNav' @click='subOneDay'>
            <v-icon large>keyboard_arrow_left</v-icon>
        </v-btn>

        <div class='fluid__bardate__nav__title' @click='reset'>
            <span>{{ this.title }}</span>

            <div v-if='this.lastUpdate' class='fluid__bardate__nav__subtitle'>
                <span>
                    Ultima movimentao s
                    <span>{{ descriptionLastUpdate }}</span>
                </span>
            </div>
        </div>

        <v-btn flat icon :disabled='disabledNav || dateSelectedIstoday' @click='addOneDay'>
            <v-icon large>keyboard_arrow_right</v-icon>
        </v-btn>
    </div>
</template>

